.about_section {
  background-color: #fbfff7;
  padding: 6rem 0 2rem;

  .about_title {
    display: flex;
    justify-content: center;
    text-align: center;
    padding-bottom: 2rem;
  }

  .container {
    margin-left: auto;
    margin-right: auto;
    max-width: 968px;
  }
  .grid {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }
  .about_description {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .about_button {
    text-decoration: none;
    border-radius: 8px;
    width: 200px;
    height: 50px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    margin-top: 25px;
    background-color: #acd8b8;
    font-size: 20px;
    font-weight: 500;
  }

  .about_button > svg {
    width: 24px;
    height: 24px;
    margin-left: 5px;
  }
  .about_button:hover {
    background-color: #d8e5cc;
  }
}

@media screen and (max-width: 768px) {
  .about_section {
    .grid {
      grid-template-columns: 1fr;
      grid-gap: 0;
      gap: 0;
    }
    .about_avatar {
      width: 90%;
      margin: 0 auto;
    }
    .about_description {
      align-items: center;
    }

    .about_description > p {
      padding: 0 20px;
    }
    .about_button {
      margin-top: 20px;
    }
  }
}
